:root{
  --background-color: rgba(102, 209, 209, 0.192);
}
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  background-color:var(--background-color);
  font-family: "Inter", sans-serif;
}

.container {
  max-width: 1000px;
  margin: 5% auto;
  margin-bottom: 0;
  padding: 0 20px;
  position: relative;
}
.cards--container {
  display: grid;
  /* grid-template-columns: repeat(4, 1fr); */
  grid-template-columns: repeat(auto-fit, minmax(12rem, 1fr));
  gap: 2rem;
}
.card {
  background-color: #fff;
  padding: 25px;
    border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
  position: relative;
}
.sm-card{
    background-color: #fff;
  padding: 25px;
    border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
  position: relative;
  max-width: 500px;
}
.sm-card img{
  width: 100%;
}
.sm-card svg{
  font-size: 1.2rem;
  color: #000;
}
.card svg{
  font-size: 1.2rem;
  color: #000;
}
.user--details{
margin-top: 10px;
}
.user--details a{
    text-decoration: none;
    color: rgb(38, 107, 197);
}
.user--details h4{
    margin-bottom: 10px;
}
.card img {
  width: 100%;
}
.badge--admin{
  background-color: rgb(211, 34, 28);
  color: #fff;
padding: 10px;
position: absolute;
top: 8.8%;
right: 11.2%;
}
.badge--user{
  background-color: rgb(22, 170, 88);
  color: #fff;
padding: 10px;
position: absolute;
top: 8.8%;
right: 11.2%;
}
.form{
  margin: 5% auto;
}
input{
  padding: 15px;
  width: 100%;
  border: none;
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
}
input:focus{
  outline: none;
}
h3{
  padding: 20px 0;
}
.user--details{
  display:flex;
  justify-content: space-between;
  padding-top: 10px;
}
footer{
padding: 0.5rem 0;
  background-color:rgba(22, 66, 66, 0.623);
  margin-top: 20%;
  color: rgba(255, 255, 255, 0.74);
}
footer .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.social--links li{
  list-style: none;
}
.social--links li a{
  padding: 10px;
}
.social--links li svg{
  color: #fff;
  font-size: 130%;
}
@media only screen and (max-width:580px){
  footer .container{
    flex-wrap: wrap;
    gap: 20px;
  }
  .details--info{
    flex-wrap: wrap;
  }
  .details--info img{
   width: 100%;
  }
}
.details--info{
  display: flex;
  gap: 2rem;
  padding-top: 5%;
}
.details--info img{
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
}
.button{
  text-decoration: none;
  display: block;
  background-color: rgb(51, 45, 45);
  margin: 20px 0;
  padding: 10px;
  color: #fff;
}
.detail-content svg{
  margin-right: 15px;
}